.password-wrapper{display:flex}.password-wrapper .password-input{display:flex;border-right:none!important}.password-wrapper .password-input .input:focus{border-right:none!important}.password-wrapper .password-input .ssn-1{display:flex;justify-content:center;width:26px}.password-wrapper .password-input .ssn-2{display:flex;justify-content:center;width:20px}.password-wrapper .password-input .ssn-3{display:flex;justify-content:center;width:35px}.password-wrapper .password-input>input{border:none;background-color:inherit}.password-wrapper .password-input>input:focus{outline:0}.password-wrapper .eye{display:flex;align-items:center;cursor:pointer;border-right:var(--borderLine);border-top:var(--borderLine);border-bottom:var(--borderLine);border-top-right-radius:3px;border-bottom-right-radius:3px}.password-wrapper .eye img{margin-right:12px;margin-left:12px}.password-input.disabled{background-color:var(--lightGrey)}.password-input.disabled input:disabled{cursor:not-allowed}.password-input.disabled+.eye{background:var(--lightGrey);border:1px solid var(--lightGrey);cursor:not-allowed}