questionCircle.flex {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.flex > * {
  margin-bottom: 10px;
  margin-right: 10px;
}

.top-container > * {
  margin-bottom: 5px;
  color: rgb(0, 78, 129);
}

.error-message {
  color: #c60f13;
  font-weight: 600;
}

body,
#root,
.App {
  min-height: 100%;
  height: 100%;
  background-color: #fafafa;
}

html {
  min-height: 100%;
  height: 100%;
}

.card {
  border-radius: 3px;
  width: 100%;
  margin: 0 auto;
  margin-top: 10px;
  background-color: white;
  padding: 20px 20px;
  border: var(--borderLine);
}

