.password-input{display:flex}.password-input .eye{display:flex;align-items:center;cursor:pointer;border-right:var(--borderLine);border-top:var(--borderLine);border-bottom:var(--borderLine);border-top-right-radius:3px;border-bottom-right-radius:3px}.password-input .eye img{margin-right:12px;margin-left:12px}.password-input>.input{border-top-right-radius:0;border-bottom-right-radius:0;border-right:0px}.password-input>.input:focus{border-right:none!important}.password-input.disabled .eye{background:var(--lightGrey);border:1px solid var(--lightGrey);cursor:not-allowed}