.Checkbox-toggle-Item{cursor:pointer;user-select:none;display:flex;width:fit-content;align-items:center}.Checkbox-toggle-Item.disabled{cursor:not-allowed}.Checkbox-toggle-Item :first-child{margin-right:12px}.Checkbox-toggle-Item .checkbox-toggle{display:flex;align-items:center;width:16px;height:16px;border:var(--borderLine);border-radius:3px;background-color:var(--white)}.Checkbox-toggle-Item .checkbox-toggle .input-error{border:2px solid var(--red)}.Checkbox-toggle-Item .checkbox-toggle .svg-wrapper{height:14px;width:14px;filter:var(--svgWhite)}.Checkbox-toggle-Item .checkbox-toggle:focus:not(:focus-visible){box-shadow:none;outline:0}.Checkbox-toggle-Item .checkbox-toggle:focus-visible{box-shadow:0 0 0 4px var(--lightBlue);outline:0}.Checkbox-toggle-Item .checkbox-toggle.checked{background-color:var(--blue)}.checkbox-toggle-container{width:100%}.checkbox-toggle-container .label-container{display:none}.checkbox-toggle-container .desc-label{max-width:300px}.checkbox-toggle-label{display:flex}.checkbox-toggle.disabled{background-color:var(--lightGrey)}.checkbox-toggle.disabled .svg-wrapper{filter:var(--svgGrey)}