.includes-excludes-option{border:thin solid var(--lightBlue)}.includes-excludes-option:last-child{border-left:0;border-top-right-radius:3px;border-bottom-right-radius:3px}.selection-container{display:flex;width:100%}.selection-container>.input{border-right:none!important;border-top-right-radius:0!important;border-bottom-right-radius:0!important}.selection-container>.input:focus{border-top-right-radius:0!important}.selection-container>div{display:flex;flex-basis:20%;justify-content:center;align-items:center;user-select:none;color:var(--lightBlue)}.selection-container>div:hover{cursor:pointer}.selection-container:first-child{flex-basis:60%;border-right:0}.selected{background-color:var(--lightBlue);color:#fff!important;animation-delay:0s}