.toggle-pill-container .toggle-pill{position:relative;display:flex;align-items:center;height:42px;width:90px;border-radius:25px;background-color:var(--blue);padding:4px;cursor:pointer}.toggle-pill-container .toggle-pill:focus:not(:focus-visible){box-shadow:none;outline:0}.toggle-pill-container .toggle-pill:focus-visible{box-shadow:0 0 0 4px var(--lightBlue);border:4px solid var(--white);height:50px;width:98px;outline:0}.toggle-pill-container .toggle-pill.disabled{user-select:none;background-color:var(--grey);cursor:not-allowed}.toggle-pill-container .toggle-pill .inner-circle{height:100%;width:45%;border-radius:50%;background-color:var(--white)}.toggle-pill-container .toggle-pill .inner-circle.mousedown{box-shadow:0 0 3px 0 var(--blue)}