.password-input{position:relative;display:flex;width:100%}.password-input>.input{border-top-right-radius:0;border-bottom-right-radius:0;border-right:0px}.password-input .add{display:flex;align-items:center;cursor:pointer;border-right:var(--borderLine);border-top:var(--borderLine);border-bottom:var(--borderLine);border-top-right-radius:3px;border-bottom-right-radius:3px}.password-input .add .svg-wrapper{width:24px;height:24px}.password-input .add img{margin-right:12px;margin-left:12px}.multi-select-container{background-color:#fafafa;border:solid 1px var(--lightBlue);border-top:none;padding:10px;display:flex;flex-direction:column;align-items:center}.multi-select-container>.multi-select-option{margin-right:5px;margin-bottom:5px;border:solid 1px var(--lightBlue);background-color:var(--hoverBlue);font-weight:600;border-radius:3px;width:95%;display:flex;justify-content:space-between;padding-left:5px}.multi-select-container>.multi-select-option:hover{background-color:var(--hoverBlue)}.multi-select-container>.multi-select-option>*{align-self:center}.multi-select-container>.multi-select-option .image-container{background-color:var(--hoverBlue)}.multi-select-container>.multi-select-option .image-container:hover{background-color:var(--lightBlue)}.multi-select-container>.multi-select-option .image-container>img{cursor:pointer;background-color:transparent}.password-input.disabled .add{background:var(--lightGrey);border:1px solid var(--lightGrey);cursor:not-allowed}.password-input.disabled .add .svg-wrapper{filter:var(--svgGrey)}