.FileUploadComponent{padding:10px 8px!important;display:flex;flex-direction:column;align-items:center}.FileUploadComponent .multi-select-container{width:100%;display:flex;flex-wrap:wrap;position:relative;margin-top:10px;border:none;padding:0}.FileUploadComponent .multi-select-container .selected-option{width:100%;display:flex;justify-content:space-between;align-items:center;padding:8px;border:var(--borderLine)}.FileUploadComponent .multi-select-container .selected-option:not(.FileUploadComponent .multi-select-container .selected-option:first-child){border-top:none}.FileUploadComponent .multi-select-container .selected-option:hover{background-color:var(--lighterGrey)}.FileUploadComponent .multi-select-container .selected-option img:hover{background-color:var(--lightGrey);cursor:pointer}.FileUploadComponent::-webkit-file-upload-button{visibility:hidden;width:0}