.Checkbox-Item{display:flex;min-width:45px;padding:5px;justify-content:space-between;cursor:pointer;user-select:none}.Checkbox-Item :first-child{margin-right:15px}.Checkbox-Item .checkbox{display:flex;width:14px!important;height:14px!important;border:2px solid var(--lightBlue);border-radius:3px;background-color:#fff}.Checkbox-Item .checkbox.checked{background-color:var(--lightBlue)}.Checkbox-Item .checkbox>input{appearance:none}.checkbox-container{display:flex;flex-direction:column}