.FormWrapperComponent .InputWrapperComponent{margin-bottom:10px}.FormWrapperComponent .btn-container{background-color:#fff;width:100%;flex-direction:row!important;justify-content:flex-end;padding:10px}.FormWrapperComponent .btn-container>:last-child{margin-left:20px!important}.FormWrapperComponent>.container{display:flex;align-items:center;flex-direction:column;padding-top:10px;transition:width .2s}.FormWrapperComponent>.container>.header{color:var(--fontColor)!important;width:100%}.FormWrapperComponent>.container .card{display:flex;flex-direction:column}.FormWrapperComponent>.container .card .InputComponent{width:100%;max-width:100%}@media only screen and (min-width:320px){.FormWrapperComponent>.container{width:95%;padding:0;margin-bottom:10px}.FormWrapperComponent>.container .card{display:flex;flex-direction:column}.FormWrapperComponent>.container .card .InputComponent{width:100%;max-width:100%}}@media only screen and (min-width:768px){.FormWrapperComponent>.container{width:80%}}@media only screen and (min-width:900px){.FormWrapperComponent>.container{max-width:50%}}@media only screen and (min-width:1200px){.FormWrapperComponent>.container{width:40%}}@media only screen and (min-width:1400px){.FormWrapperComponent>.container{width:30%}}