.SelectInputListItem{position:relative;padding:8px 12px;cursor:pointer;border:transparent solid 2px;border-bottom:2px solid var(--lightGrey);outline:0}.SelectInputListItem:hover{background-color:var(--hoverBlue)}.SelectInputListItem:focus,.SelectInputListItem:focus-visible{outline:0;border:2px solid var(--lightBlue)!important}.select-dropdown{position:absolute;width:100%;border-radius:3px;background-color:var(--white);border:var(--borderLine);transition:all 0s;top:100%;border-top:none;z-index:2}.SelectInputComponent{display:flex;flex-direction:column;position:relative}.custom-select{position:relative;display:flex;align-items:center;user-select:none;justify-content:space-between;background-color:var(--white);cursor:pointer;min-width:100%;max-width:100%}.custom-select.disabled{user-select:none;cursor:not-allowed}.custom-select img{margin-left:12px;width:20px;height:20px}