.TablePagination{display:flex;flex-direction:row-reverse;flex-wrap:nowrap;justify-content:space-between;align-items:center;align-content:flex-end}.TablePagination .TP-rows-container>*,.TablePagination .rows-container,.TablePagination>*>*,.TablePagination>*>*>*{display:flex;align-items:center}.TablePagination>.TablePagination-container{display:flex;flex-direction:column}.TablePagination .TP-rows-container>div:hover{background-color:#ebebff;cursor:pointer}.TablePagination .bottom-container{justify-content:flex-start;max-height:0;opacity:0;pointer-events:none;transition:all .1s}.TablePagination .bottom-container.bottom-container-is-displayed{max-height:1000px;opacity:1;pointer-events:initial;transition:max-height 1s}.TablePagination .bottom-container>span{padding:10px;cursor:pointer;border-radius:5px;font-weight:600;display:flex;justify-content:center;width:30px}.TablePagination .bottom-container>.selected-row-per-page{background-color:#ebebff}.TablePagination .bottom-container>span:hover{background-color:#cecefd}.end-of-rows-style{position:absolute!important;width:100%}