.InputWrapperComponent{position:relative}.InputComponent{display:flex;flex-direction:column;margin-bottom:16px}.InputComponent .label-container{display:flex;margin-bottom:6px}.InputComponent .label-container label .input-error{color:var(--red)}.InputComponent .input{width:100%;padding:8px 12px;background-color:var(--white);outline:0;border:var(--borderLine);border-radius:3px}.InputComponent .input:placeholder-shown{color:var(--grey);opacity:1}.InputComponent .input.disabled,.InputComponent .input:disabled{background:var(--lightGrey);border:1px solid var(--lightGrey);cursor:not-allowed}.InputComponent .input.input-error{border:2px solid var(--red)}.InputComponent .input:focus:not(.disabled){box-shadow:inset 0 0 4px 0 rgba(0,0,0,.5)}.InputComponent .input.tab-focus:focus:not(.disabled):not(.input-error){border:2px solid var(--lightBlue);padding:7px 11px}