.radio-button-container{user-select:none;cursor:pointer}.radio-button-container .label-container{display:none}.radio-button-container .radio-item{display:flex;align-items:center}.radio-button-container .radio-item.disabled{cursor:not-allowed}.radio-button-container .radio-item.disabled .radio-button{background-color:var(--lightGrey)}.radio-button-container .radio-item .radio-button{display:flex;align-items:center;justify-content:center;height:20px;width:20px;border-radius:50%;background-color:var(--white);border:var(--borderLine)}.radio-button-container .radio-item .radio-button.input-error{border:2px solid var(--red)}.radio-button-container .radio-item .radio-button:focus:not(:focus-visible){box-shadow:none;outline:0}.radio-button-container .radio-item .radio-button:focus-visible{box-shadow:0 0 0 4px var(--lightBlue);outline:0}.radio-button-container .radio-item .active{display:flex;justify-self:center;align-self:center;width:12px;height:12px;border-radius:50%;background-color:var(--blue)}.radio-button-container .radio-item .Typography{margin-left:12px}