.PhoneNumberComponent{display:flex;cursor:text}.PhoneNumberComponent .input.disabled{box-shadow:none;cursor:not-allowed}.PhoneNumberComponent>input{border:none;background-color:inherit}.PhoneNumberComponent>input:disabled{cursor:inherit}.PhoneNumberComponent>input:focus{outline:0}.PhoneNumberComponent.tab-focus:focus-within:not(.disabled){border:2px solid var(--lightBlue);padding:7px 11px}.PhoneNumberComponent:focus-within:not(.disabled){box-shadow:inset 0 0 4px 0 rgba(0,0,0,.5)}