.external-nav-modal{z-index:999;position:fixed;top:0;left:0;width:100%;height:100%;justify-content:center;align-items:center;background-color:rgba(0,0,0,.5);backdrop-filter:blur(4px)}.external-nav-modal>div.card{width:100%;height:100%;position:absolute;border-radius:0;display:flex;flex-direction:column;justify-content:space-between;line-height:1.2;padding:0;margin-top:0}@media (min-width:768px){.external-nav-modal>div.card{width:80%;height:40%;margin-top:10px;left:10%;top:10%;border-radius:20px;overflow:hidden}}@media (min-width:992px){.external-nav-modal>div.card{width:70%;height:30%;left:15%}}@media (min-width:1500px){.external-nav-modal>div.card{width:40%;height:35%;left:30%}}.external-nav-modal>div.card>h3.navigate-header{background-color:var(--lightBlue);padding:20px;font-size:1.5rem;color:#fff;text-align:center}.external-nav-modal>div.card>p{font-size:1rem;font-weight:500;padding:20px}.external-nav-modal>div.card>div{display:flex;flex-direction:column;justify-content:flex-end;padding:20px}@media (min-width:768px){.external-nav-modal>div.card>div{flex-direction:row}}.external-nav-modal>div.card>div :first-child{margin-bottom:5px}@media (min-width:768px){.external-nav-modal>div.card>div :first-child{margin-bottom:0;margin-right:5px}}.external-nav-modal>div.card>div>a{display:flex;text-decoration:none}.external-nav-modal>div.card>div>a>div{width:100%}.external-nav-modal>div.card>div>div{display:flex;width:100%;justify-content:flex-end}@media (min-width:768px){.external-nav-modal>div.card>div>div{width:max-content}.external-nav-modal>div.card>div>div>:last-child{margin-left:15px}}